import React, { useEffect, useState } from 'react'
import './style.css'

import { useActivePopup } from '../../../../store/ActivePopup';
import { useColourObjectStore } from '../../../../store/ColourStore';
import { useLoading } from '../../../../store/Loading';
import Cookies from 'components/Common/Cookies';
import { instancestore } from '../../../../store/InstanceStore';
import { useParams } from 'react-router-dom';


const StartScreen = (props: any) => {

  const { colourObj } = useColourObjectStore();
  const { instance, activeInstanceData, loadinstance, loadactiveInstancebyID } = instancestore();
  let params = useParams();

  const { setActivePopup } = useActivePopup();

  const { Loading, pushLoading, popLoading, isLoading } = useLoading();

  useEffect(() => {
    console.log("params.id::::", params.instanceId);


    if (!instance) {
      loadinstance()
      // loadactiveInstancebyID(params.instanceId)
      loadactiveInstancebyID(params.instanceId)
    }

  }, [activeInstanceData])

  return (
    <div>
      <div className="bottomband">

        <div className="sublevel_bodytext" style={{ color: colourObj.color_m1 }}>

          {activeInstanceData?.i_body ?
            <div className="starttext">
              {activeInstanceData?.i_stitle}
              {activeInstanceData?.i_body}
            </div>
            :
            <div className="starttext">
              Welcome to DULT
            </div>
          }

        </div>

        <div className="login_button_container">
          <button className="login_button shadow" style={{ color: colourObj.color_m3, backgroundColor: colourObj.color_m1, borderColor: colourObj.color_m3 }}
            onClick={() => { setActivePopup("login") }}>LOGIN</button>
        </div>

        <Cookies />

      </div>

    </div >
  )

}

export default StartScreen