import React, { useEffect, useState } from 'react';
import './style.css';
import { useParams } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import * as close from '../../../../../assets/Clean Walk/close.png';
import { sendGamedata } from 'components/Common/core';
import { useAuth } from '../../../../../store/authStore'
import { userActiveSubLevelDetailsStore } from '../../../../../store/ActiveSubLevelDetailsStore';
import { useLanguageStore } from '../../../../../store/LanguageStore';
import { useColourObjectStore } from '../../../../../store/ColourStore';

import { userActiveLevelDetailsStore } from '../../../../../store/ActiveLevelDetailsStore';
import { useActivePopup } from '../../../../../store/ActivePopup';
import { useActiveScreen } from '../../../../../store/ActiveScreen';

const QuizComponent = (props: any) => {

  const { colourObj } = useColourObjectStore();
  const { ActivePopup, setActivePopup } = useActivePopup();
  const { setActiveScreen } = useActiveScreen();
  const { ActiveLevelDetails, loadActiveleveldetails } = userActiveLevelDetailsStore();
  const [spinner, setspinner] = useState(true)
  const { auth } = useAuth();
  const { loadActiveSubleveldetails } = userActiveSubLevelDetailsStore();
  const { Language, setLanguage } = useLanguageStore();
  const [popup, setpopup] = useState(false)
  const [quiz_submit, setquiz_submit] = useState(false)
  const [list, setlist] = useState({
    "Quiz": "1",
    "QuestionType": "Options",
    "Title": "CHALLENGE",
    "Question": "What was the last place you walked to?",
    "subHeading": "Did You Know?",
    "FunFact": "International guidelines recommend about 7000 steps a day for good physical and mental well being.",
    "source": "null",
    "Optiondata": [
      {
        "Option": "1",
        "Answer": "The grocery store",
        "active": "true"
      },
      {
        "Option": "2",
        "Answer": "The parking lot",
        "active": "false"

      },
      {
        "Option": "3",
        "Answer": "The bathroom",
        "active": "false"

      },
      {
        "Option": "4",
        "Answer": "Churchstreet",
        "active": "false"

      }
    ]
  })

  let params = useParams();

  const history = useHistory();

  useEffect(() => {
    setspinner(false)
    console.log("propse: anye: any :", props);
  }, [])

  const getoptionlist = (options: String) => {
    // console.log("======", options);
    let arr = options?.split("/")
    // console.log("===arr===", arr);
    return (
      arr.map((opt: any) => {
        return <>{props.GameDataLog[0]?.data === opt ?
          <div className="option_button_Green"
            style={{ backgroundColor: colourObj.color_green }}
          // onClick={() => {
          //   console.log("option ::::", opt);
          //   sendGamedata(props.id, "SUBLEVEL", opt, "", "DONE", auth)
          //   loadActiveSubleveldetails(props.id)
          // }}
          >{opt}</div>
          :
          <div className="option_button_Orange"
            style={{ backgroundColor: colourObj.color_m2 }}
            onClick={() => {
              console.log("option ::::", opt);
              sendGamedata(props.id, "SUBLEVEL", opt, "", "DONE", auth)
              loadActiveSubleveldetails(props.id)
              setquiz_submit(true)
            }}
          >{opt}</div>
        }</>
      })
    )
  }

  return (

    <div className="sublevel_wrapper">
      {popup &&
        <div className="popup_bg">
          <div className="popup_container">
            <div className="popup_closecontainer" style={{ backgroundColor: colourObj.color_m2 }}>
              <img onClick={() => { setpopup(false) }} className='quiz_close' src={close} />
            </div>
            <div className="popup" style={{ backgroundColor: colourObj.color_t1, borderColor: colourObj.color_m2 }}>

              <div className='Title_OrangeU' style={{ color: colourObj.color_m1, textDecorationColor: colourObj.color_m2 }}>{list.subHeading} </div>
              <div className='popup_Text' style={{ color: colourObj.color_s1 }}>{list.FunFact}</div>
              {list.source !== "null" ? <div
                onClick={() => {
                  window.open(list.source)
                }} className='popup_source' style={{ color: colourObj.color_m2 }}>
                source
              </div> : null}

            </div>
            <div className='popup_continue_buttonContainer'>
              <button onClick={() => {
                history.replace('/RewardScreen/' + params.id)
              }} className="continue_button_Orange" style={{ backgroundColor: colourObj.color_m2 }}>
                <div className="button_text" style={{ color: colourObj.color_m1 }}>Continue</div>
              </button>
            </div>
          </div>
        </div>
      }

      {(props.sl_bodytype === "OPTIONS") ?
        <>
          <div className="sublevel_subtitle_container">
            <div className='Title_OrangeU' style={{ color: colourObj.color_m1, textDecorationColor: colourObj.color_m2 }}>{(Language?.value === "KANNADA") ? props.TextData[0]?.title : props.sl_title}</div>
            {props.sl_stitle !== undefined && <div className='sublevel_subtitle'>{(Language?.value === "KANNADA") ? props.TextData[0]?.s_title : props.sl_stitle}</div>}
          </div>

          <div className="sublevel_body_container">
            {props.sl_body !== undefined && <div className='sublevel_bodytext' style={{ color: colourObj.color_m1 }}>
              {(Language?.value === "KANNADA") ? props.TextData[0]?.body : props.sl_body}</div>}
            {getoptionlist((Language?.value === "KANNADA") ? props.TextData[0]?.bodyoptions : props.sl_bodyoptions)}
          </div>
        </>
        :
        <>
          <div className="sublevel_subtitle_container">
            <div className='Title_OrangeU' style={{ color: colourObj.color_m1, textDecorationColor: colourObj.color_m2 }}>{(Language?.value === "KANNADA") ? props.TextData[0]?.s_title : props.sl_title}</div>
            {props.sl_stitle !== undefined && <div className='sublevel_subtitle'>{(Language?.value === "KANNADA") ? props.TextData[0]?.s_title : props.sl_stitle}</div>}
          </div>

          <div className="sublevel_body_container">
            {props.sl_body !== undefined && <div className='sublevel_bodytext' style={{ color: colourObj.color_m1 }}>
              {(Language?.value === "KANNADA") ? props.TextData[0]?.body : props.sl_body}</div>}
            <input id="inputbox" placeholder={props.GameDataLog[0]?.data} className='quiz_input' type="text" />
            <button className="continue_button_Orange" style={{ backgroundColor: colourObj.color_m2 }}
              onClick={() => {
                // console.log(">>", String(document.getElementById("inputbox").value));
                sendGamedata(props.id, "SUBLEVEL", String(document.getElementById("inputbox").value), "", "DONE", auth)
                setTimeout(() => {
                  loadActiveSubleveldetails(props.id)
                  loadActiveleveldetails(props.level_id)
                }, 500);
                setTimeout(() => {
                  ((Number(((ActiveLevelDetails) && ActiveLevelDetails.progress))) === 100) ?
                    setActivePopup("reward")
                    :
                    setActivePopup("submit")
                }, 1000);

                // console.log("12345798", props)
              }}>
              <div className="button_text" style={{ color: colourObj.color_m1 }}>SUBMIT</div>
            </button>
          </div>
        </>
      }


      {/* <button className="continue_button_Orange"
        style={{ backgroundColor: colourObj.color_m2 }}
        onClick={() => {
          // console.log("quiz_submit", quiz_submit);

          if (quiz_submit) {
            // sendGamedata(props.id, "SUBLEVEL", opt, "", "DONE", auth)
            setTimeout(() => {
              loadActiveSubleveldetails(props.id)
              loadActiveleveldetails(props.level_id)
            }, 500);
            setTimeout(() => {
              ((Number(((ActiveLevelDetails) && ActiveLevelDetails.progress))) === 100) ?
                setActivePopup("reward")
                :
                setActiveScreen("Level")
            }, 1000);
            // console.log("12345798", props)
          }
        }}>
        <div className="button_text" style={{ color: colourObj.color_m1 }}>SUBMIT</div>
      </button> */}

    </div>

  )
}

export default QuizComponent