import React, { useEffect, useState } from 'react'
import { useLanguageStore } from '../../../../../store/LanguageStore';
import './style.css'
import { useColourObjectStore } from '../../../../../store/ColourStore'
import Animationcomponent from 'components/Common/Animation';


const VideoComponent = (props: any) => {

  const { aniout } = props;

  const { colourObj, loadcolor } = useColourObjectStore();

  const { Language, setLanguage } = useLanguageStore();

  // const fade = (props) => {
  //   let result = (aniout ? (props + " " + "fade_out") : (props))
  //   // console.log(result);
  //   return result
  // }

  // const fadezoom_out = (props) => {
  //   let result = (aniout ? (props + " " + "fadezoom_out") : (props))
  //   // console.log(result);
  //   return result
  // }

  return (
    <div className='videoscreen_container'>
      {/* <img className='sublevel_logo' src={props?.sl_icon} /> */}
      <Animationcomponent
        className={'video_text_wrapper'}
        Animationtype={"fade_out"}
        aniout={aniout}
      >
        <div className='video_title'>{(Language?.value === "KANNADA") ? props.TextData[0]?.title : props?.sl_title}</div>
        <div className='video_subtitle'>{(Language?.value === "KANNADA") ? props.TextData[0]?.s_title : props?.sl_stitle}</div>
        <div className='video_body'>{(Language?.value === "KANNADA") ? props.TextData[0]?.body : props?.sl_body}</div>
      </Animationcomponent>

      <Animationcomponent
        className={"videoplayer"}
        Animationtype={"fadezoom_out"}
        aniout={aniout}
      >
        <div className="loader" style={{ borderColor: colourObj.color_m2 }} />
        <iframe className="iframe_videoplayer" src={props?.sl_media} width="100%" height="100%" frameBorder="0"></iframe>
      </Animationcomponent>
    </div>
  )
}

export default VideoComponent