import React, { useEffect, useRef, useState } from 'react'
import './style.css'

import Level from 'components/Game/Screens/Level'
import Splash from 'components/Splash'
import SubLevel from 'components/Game/Screens/SubLevel'
import StartScreen from 'components/Game/Screens/Start'
import Home from 'components/Game/Screens/Home'

import { useActiveScreen } from '../../store/ActiveScreen';
import { useActivePopup } from '../../store/ActivePopup';
import { useAuth } from '../../store/authStore'

import { useHistory, useLocation, useParams } from 'react-router-dom';
import QRscanner from 'components/Game/Popups/QRscanner'

import { instancestore } from '../../store/InstanceStore';
import { useColourObjectStore } from '../../store/ColourStore'
import { useLanguageStore } from '../../store/LanguageStore'
import { userActiveLevelDetailsStore } from '../../store/ActiveLevelDetailsStore'
import { userActiveSubLevelDetailsStore } from '../../store/ActiveSubLevelDetailsStore'

import ScreenTemplate from 'components/Game/Screens'

import * as lsqr from '../../assets/images/Levels_Screen/LS_qr.png'
import Popup from 'components/Common/Popup'
import Settings from 'components/Game/Popups/Settings'
import Share from 'components/Game/Popups/Share'
import EditProfile from 'components/Game/Popups/EditProfile'
import LoginPopup from 'components/Game/Popups/LoginPopup'
import Reward from 'components/Game/Popups/Reward'
import Submit from 'components/Game/Popups/Submit'
import { levelstore } from '../../store/levelStore'
import Animation from '../Common/Animation'
import Animationcomponent from '../Common/Animation'
const Game = () => {

  const { level } = levelstore();

  const { colourObj, loaddefaultcolor, loadinstancecolor } = useColourObjectStore();

  const [instancedata, setinstancedata] = useState({})

  const { instance, activeInstanceData, loadinstancewithcallback, setactiveInstanceData, loadinstance, loadactiveInstancebyID } = instancestore();

  const { ActiveScreen, setActiveScreen } = useActiveScreen();
  const { ActivePopup, setActivePopup } = useActivePopup();

  const { auth } = useAuth();

  const search = useLocation().search;

  const { Language } = useLanguageStore();
  const { loadActiveleveldetailsbyqr } = userActiveLevelDetailsStore();
  const { ActiveLevelDetails, loadActiveleveldetails } = userActiveLevelDetailsStore();
  const { ActiveSubLevelDetails, loadActiveSubleveldetails } = userActiveSubLevelDetailsStore();

  const [completed, setcompleted] = useState(false)

  const [confirm, setConfirm] = useState(false);

  const [aniout, setaniout] = useState(false);

  const history = useHistory();
  let params = useParams();

  useEffect(() => {

    console.log("ActiveScreen", ActiveScreen);

    if (!auth) {
      loaddefaultcolor()
      setActiveScreen("Splash")
    }
    else {
      if (!instance) {
        loaddefaultcolor()
        loadinstance()
      } else {
        if (activeInstanceData) {
          // console.log("INSTANCE DATA DATA >>>>> ", activeInstanceData)

          loadinstancecolor(activeInstanceData.id)
          setinstancedata(activeInstanceData)
        }
        if (!ActiveLevelDetails) {
          loadActiveleveldetails(localStorage.getItem("ActiveLevel"))
        }
        if (!ActiveSubLevelDetails) {
          loadActiveSubleveldetails(localStorage.getItem("ActiveSubLevel"))
          setcompleted(Number(((ActiveLevelDetails) && ActiveLevelDetails.progress)) === 100 ? true : false)
        }
      }
    }

  }, [auth, instance, activeInstanceData])



  const RenderScreen = () => {
    if (ActiveScreen === "Splash") {
      return <Splash />
    } else if (ActiveScreen === "StartScreen") {
      return <StartScreen />
    } else if (ActiveScreen === "Home") {
      return <Home aniout={aniout} setaniout={setaniout} />
    } else if (ActiveScreen === "Level") {
      return <Level aniout={aniout} setaniout={setaniout} />
    } else if (ActiveScreen === "SubLevel") {
      return <SubLevel aniout={aniout} setaniout={setaniout} />
    } else
      return <> </>
  }

  const RenderPopup = () => {
    if (ActivePopup === "QR") {
      return <QRscanner />
    } else if (ActivePopup === "settings") {
      return <Settings />
    } else if (ActivePopup === "share") {
      return <Share />
    } else if (ActivePopup === "editprofile") {
      return <EditProfile confirm1={() => { confirm }} />
    } else if (ActivePopup === "login") {
      return <LoginPopup />
    } else if (ActivePopup === "reward") {
      return <Reward completion_image={ActiveLevelDetails.completion_image} completion_text={ActiveLevelDetails.completion_text} />
    } else if (ActivePopup === "completed_game") {
      return <Reward completion_image={ActiveLevelDetails.completion_image} completion_text={ActiveLevelDetails.completion_text} completed_game={true} />
    } else if (ActivePopup === "submit") {
      return <Submit />
    } else {
      return <> </>
    }
  }

  const LanguageBasedPredefinedThings = () => {
    let data = {}
    if (Language?.value === "KANNADA") {
      switch (ActivePopup) {
        case "settings":
          data = {
            title: "ಸಂಯೋಜನೆಗಳು"
          }
          break;
        case "share":
          data = {
            title: "ಶೇರ್ ಮಾಡಿ"
          }
          break;
        case "profileedit":
          data = {
            title: "ಪ್ರೊಫೈಲ್ ಎಡಿಟ್"
          }
          break;
        case "login":
          data = {
            title: "LOGIN",
            desc1: "Please select your preferred method to login."
          }
      }
    }
    else {
      switch (ActivePopup) {
        case "settings":
          data = {
            title: "SETTINGS"
          }
          break;
        case "share":
          data = {
            title: "SHARE"
          }
          break;
        case "profileedit":
          data = {
            title: "EDIT PROFILE"
          }
          break;
        case "login":
          data = {
            title: "LOGIN",
            desc1: "Please select your preferred method to login."
          }
      }
    }
    return data
  }

  const navigate = (object: any) => {
    if (getgameprogress() === 100) {
      setActivePopup("completed_game")
    } else {
      setActiveScreen("Level")
      loadActiveleveldetails(object.id)
      localStorage.setItem("ActiveLevel", object.id)
      setActivePopup("")
    }
  }

  const getgameprogress = () => {
    let a = 0
    let b = level && level.length
    level && level.map((obj) => { a = a + Number(obj.progress) })
    return (a / b)
  }

  return (
    <>
      <ScreenTemplate
        aniout={aniout}
        setaniout={setaniout}
        instancedata={instancedata}
        screen_name={(ActiveScreen === "SubLevel") ? (ActiveSubLevelDetails.sl_types + " " + ActiveScreen) : ActiveScreen}
        Rewardscreen={((ActiveScreen === "Level") && completed)}
        setcompleted={(ActiveScreen === "Level") && setcompleted}
        isCompleted={(ActiveScreen === "Level") && (ActiveLevelDetails) && ActiveLevelDetails.progress === 100}
        main_body={RenderScreen()}
        footer_body={(ActiveScreen === "Home") &&
          <Animationcomponent
            onClick={() => {
              setaniout(true);
              setTimeout(() => {
                setActivePopup("QR")
              }, 290);
            }}
            aniout={aniout}
            className={'task_scanqrcontainer'}
            Animationtype={"fade_out"}
            style={aniout ? { backgroundColor: colourObj.color_m1 }
              : { backgroundColor: colourObj.color_m1, opacity: 0, animationDelay: '1.6s' }}>
            <img className='task_scanqr' src={lsqr} />
          </Animationcomponent>
        }
      />

      {(ActivePopup.length > 0) &&
        <Popup
          classname={"game"}
          title={LanguageBasedPredefinedThings().title}
          desc1={LanguageBasedPredefinedThings().desc1}
          desc2={LanguageBasedPredefinedThings().desc2}
          popup_body={RenderPopup()}
          confirmClick={() => { setConfirm(true) }}
          cancelClick={() => {
            (ActivePopup === "reward") ? navigate(ActiveLevelDetails.id) : (ActivePopup === "completed_game") ? setActiveScreen("Home") : setActivePopup("")
          }}
          actionable={(ActivePopup === "editprofile") ? true : false}
          back_btn={((ActivePopup === "editprofile") || (ActivePopup === "QR")) || (ActivePopup === "completed_game") ? false : true}
        />
      }
    </>
  )
}
export default Game;
