import React, { useState, useEffect } from 'react'
import './style.css'
import * as close from '../../../../../assets/Clean Walk/Video/VID_next.png'
import * as switchcamera from '../../../../../assets/Clean Walk/rotate.svg'
import * as camer from '../../../../../assets/Clean Walk/camer_icon.png'
import * as facebook from '../../../../../assets/Clean Walk/fb.svg'
import * as twitter from '../../../../../assets/Clean Walk/twitter.png'
import useCountDown from 'react-countdown-hook';
import Slider from '@material-ui/core/Slider/Slider';
import { fileupload } from 'utils/actions';
import Camera, { FACING_MODES } from 'react-html5-camera-photo';
import 'react-html5-camera-photo/build/css/index.css';
import { ImagePreview } from 'components/Common/ImagePreview';
import { sendGamedata } from 'components/Common/core';
import { useAuth } from '../../../../../store/authStore'
import { userActiveSubLevelDetailsStore } from '../../../../../store/ActiveSubLevelDetailsStore';
import { useLanguageStore } from '../../../../../store/LanguageStore';
import { useColourObjectStore } from '../../../../../store/ColourStore';

import AudioRecord from '../AudioRecord';
import { userActiveLevelDetailsStore } from '../../../../../store/ActiveLevelDetailsStore'
import { useActivePopup } from '../../../../../store/ActivePopup';
import { useActiveScreen } from '../../../../../store/ActiveScreen';

const ActivityComponent = (props: any) => {

  const { colourObj } = useColourObjectStore();

  const { auth } = useAuth();
  const { Language, setLanguage } = useLanguageStore();

  const [camera, setcamera] = useState(false)
  const [popup, setpopup] = useState(false)

  const [share, setshare] = useState(false)

  const [timeLeft, { start, pause, resume, reset }] = useCountDown(60 * 1000, 1000);
  const [timerglow, settimerglow] = useState(false)
  const [inputvalue, setinputvalue] = useState(props?.GameDataLog[0]?.data)
  const [inputvalue1, setinputvalue1] = useState(props?.GameDataLog[0]?.data)

  const [idealFacingMode, setIdealFacingMode] = useState(false);
  const [isMaxResolution, setIsMaxResolution] = useState(false);
  const [dataUri, setDataUri] = useState(props?.GameDataLog[0]?.data);

  const [submitactivate, setsubmitactivate] = useState(false)

  const { loadActiveSubleveldetails } = userActiveSubLevelDetailsStore();
  const { ActiveLevelDetails, loadActiveleveldetails } = userActiveLevelDetailsStore();
  const { ActivePopup, setActivePopup } = useActivePopup();
  const { setActiveScreen } = useActiveScreen();

  useEffect(() => {

    console.log(">>> : : : ", props)
  }, [])


  const marks = [
    {
      value: 0,
      label: 'Unpleasant',
    },
    {
      value: 50,
      label: 'Neutral',
    },
    {
      value: 100,
      label: 'Pleasant',
    },
  ];


  const _handleTakePhotoAnimationDone = (data: any, file: any) => {

    var byteString;
    if (data.split(',')[0].indexOf('base64') >= 0)
      byteString = atob(data.split(',')[1]);
    else
      byteString = unescape(data.split(',')[1]);

    // separate out the mime component
    var mimeString = data.split(',')[0].split(':')[1].split(';')[0];

    // write the bytes of the string to a typed array
    var ia = new Uint8Array(byteString.length);
    for (var i = 0; i < byteString.length; i++) {
      ia[i] = byteString.charCodeAt(i);
    }
    console.log('takePhoto');
    let formdata = new FormData()
    formdata.append("file", new Blob([ia], { type: mimeString }))

    fileupload(Callback, auth, formdata, file)
  }

  const _onChangeHandler = (data: any, file: any) => {
    console.log(">>>>:>>>:?? :", data, file);
    let formdata = new FormData()
    let filedata = data.target.files[0]
    formdata.append("file", filedata)

    console.log(formdata);

    fileupload(Callback, auth, formdata, file)
  }

  const Callback = async (data: any, errorresponse: any) => {
    if (data.status === 201) {
      console.log("respnse :", data)
      setinputvalue(data.data.file)
      setDataUri(data.data.file)
    }
    else {
      console.log('error ' + JSON.stringify(data));
      console.log('error ' + JSON.stringify(errorresponse));
    }
  };


  return (

    <div className="sublevel_wrapper">

      <div className="sublevel_subtitle_container">
        <div className='Title_OrangeU' style={{ color: colourObj.color_m1, textDecorationColor: colourObj.color_m2 }}>{(Language?.value === "KANNADA") ? props.TextData[0]?.title : props.sl_title}</div>
        {props.sl_stitle !== undefined && <div className='sublevel_subtitle'>{(Language?.value === "KANNADA") ? props.TextData[0]?.s_title : props.sl_stitle}</div>}
      </div>

      <div className="sublevel_body_container">
        {props.sl_body !== undefined && <div className='sublevel_bodytext' style={{ color: colourObj.color_m1 }}>
          {(Language?.value === "KANNADA") ? props.TextData[0]?.body : props.sl_body}</div>}


        {(props.sl_misc === "TIMER") &&
          <div className="activity_actioncontainer">
            <div className="activity_timercontainer" style={{ borderColor: colourObj.color_15 }}>
              <div className="activity_timercontainer1" style={{ backgroundColor: colourObj.color_m1, borderColor: colourObj.color_m2, boxShadow: colourObj.color_15 }}>

                {timerglow ?
                  <div>
                    <div className='activity_timer1' style={{ color: colourObj.color_s1 }}>{(Language?.value === "KANNADA") ? "ಟೈಮರ್" : "TIMER"}</div>
                    <div className='activity_starttimer' style={{ color: colourObj.color_12 }}>{timeLeft / 1000}</div>
                  </div>
                  :
                  <div>
                    <div className='activity_timer1' style={{ color: colourObj.color_s1 }}>{(Language?.value === "KANNADA") ? "ಟೈಮರ್" : "TIMER"}</div>
                    <div className='activity_starttimer' style={{ color: colourObj.color_12 }}>{timeLeft / 1000}</div>
                  </div>
                }
              </div>

              <div>

                {timerglow ?
                  <button onClick={() => {
                    start()
                    settimerglow(!timerglow)
                  }} className="activity_buttonstart1" style={{ backgroundColor: colourObj.color_12 }}>
                    <div className="activity_buttontext1">
                      {(Language?.value === "KANNADA") ? "ಆರಂಭ/ಮರುಪ್ರಯತ್ನಿಸಿ" : "START/RETRY"}
                    </div>
                  </button>
                  :
                  <button onClick={() => {
                    start()
                    settimerglow(!timerglow)
                  }} className="activity_buttonstart_false1" style={{ backgroundColor: colourObj.color_m2 }}>
                    <div className="activity_buttontext1">
                      {(Language?.value === "KANNADA") ? "ಆರಂಭ/ಮರುಪ್ರಯತ್ನಿಸಿ" : "START/RETRY"}
                    </div>
                  </button>}

                <button onClick={() => {
                  pause()
                  settimerglow(!timerglow)
                }} className="activity_buttonstart1" style={{ backgroundColor: colourObj.color_12 }}>
                  <div className="activity_buttontext1">
                    {(Language?.value === "KANNADA") ? "ವಿರಾಮ" : "pause"}
                  </div>
                </button>

              </div>

              <div className="activity_timercontainer3_1">
                <div className="activity_timercontainer3_overlap1">
                  <div className='activity_result1'>{(Language?.value === "KANNADA") ? "ನಿಮ್ಮ ಫಲಿತಾಂಶ" : "Your Result"}</div>
                  <input id="activity_input_value" placeholder={props.GameDataLog[0]?.data} className='activity_input_timer1' type="Number" />
                </div>
              </div>

            </div>

            <div className="button_container">
              <button className="continue_button_Green" style={{ backgroundColor: colourObj.color_green, marginTop: "-5%" }}
                onClick={() => {
                  // console.log(">>", String(document.getElementById("activity_input_value").value));
                  sendGamedata(props.id, "SUBLEVEL", String(document.getElementById("activity_input_value").value), "", "DONE", auth)
                  setTimeout(() => {
                    loadActiveSubleveldetails(props.id)
                    loadActiveleveldetails(props.level_id)
                  }, 500);
                  setTimeout(() => {
                    ((Number(((ActiveLevelDetails) && ActiveLevelDetails.progress))) === 100) ?
                      setActivePopup("reward")
                      :
                      setActivePopup("submit")
                  }, 1000);
                }}>
                <div className="button_text" style={{ color: colourObj.color_m1 }}>{(Language?.value === "KANNADA") ? "ಸಲ್ಲಿಸು" : "SUBMIT"}</div>
              </button>
            </div>
          </div>
        }

        {(props.sl_misc === "INDICATOR") &&
          <div className="activity_actioncontainer">
            <div className="activity_slider_container">
              <div className="activity_slider">
                <div className='Title_OrangeU' style={{ margin: "-2vh 0 6vh", textDecoration: "none", color: colourObj.color_m1, textDecorationColor: colourObj.color_m2 }}>{inputvalue1}%</div>
                <Slider
                  step={1}
                  min={0}
                  max={100}
                  valueLabelDisplay="auto"
                  value={inputvalue1}
                  onChange={(data, value) => {
                    console.log(value, data)
                    setinputvalue1(value)
                  }}
                />
              </div>
            </div>
            <div className="button_container">
              <button className="continue_button_Green" style={{ backgroundColor: colourObj.color_green }}
                onClick={() => {
                  console.log("inputvalue1", inputvalue1);
                  sendGamedata(props.id, "SUBLEVEL", inputvalue1, "", "DONE", auth)
                  setTimeout(() => {
                    loadActiveSubleveldetails(props.id)
                    loadActiveleveldetails(props.level_id)
                  }, 500);
                  setTimeout(() => {
                    ((Number(((ActiveLevelDetails) && ActiveLevelDetails.progress))) === 100) ?
                      setActivePopup("reward")
                      :
                      setActivePopup("submit")
                  }, 1000);
                }}>
                <div className="button_text" style={{ color: colourObj.color_m1 }}>{(Language?.value === "KANNADA") ? "ಸಲ್ಲಿಸು" : "SUBMIT"}</div>
              </button>
            </div>
          </div>
        }

        {(props.sl_misc === "FILE UPLOAD") &&
          <div className="activity_actioncontainer">
            <div className="upload-wrap">
              <button type="button" className="nice-button">{(Language?.value === "KANNADA") ? "ಫೈಲ್ ಅಪ್ಲೋಡ್ ಮಾಡಿ" : "upload file"}</button>
              <input type="file" name="file" className="upload-btn" id="activity_input_value"
                onChange={_onChangeHandler}
              />
            </div>
            {
              (inputvalue !== null) ? <div>
                <img
                  className='activity_selectedimage' src={inputvalue} />
              </div> : null
            }
            <div className="button_container">
              <button className="continue_button_Green" style={{ backgroundColor: colourObj.color_green }}>
                <div onClick={() => {
                  console.log(">>>>>setinputvalue>>>>setinputvalue :", inputvalue);
                  sendGamedata(props.id, "SUBLEVEL", inputvalue, "", "DONE", auth)
                  setTimeout(() => {
                    loadActiveSubleveldetails(props.id)
                    loadActiveleveldetails(props.level_id)
                  }, 500);
                  setTimeout(() => {
                    ((Number(((ActiveLevelDetails) && ActiveLevelDetails.progress))) === 100) ?
                      setActivePopup("reward")
                      :
                      setActivePopup("submit")
                  }, 1000);
                }}
                  className="button_text" style={{ color: colourObj.color_m1 }}>{(Language?.value === "KANNADA") ? "ಸಲ್ಲಿಸು" : "SUBMIT"}</div>
              </button>
            </div>
          </div>
        }

        {(props.sl_misc === "CAMERA") &&
          <div className="activity_actioncontainer">
            <img onClick={() => {
              setcamera(true)
            }}
              className='activity_instaicon4' src={camer} />

            <div>
              <ImagePreview dataUri={dataUri} />
            </div>

            {share &&
              <div className="activity_share_fb4">
                <div className='activity__fb2main4'>
                  <img onClick={() => {
                    if (window.confirm('Strike a superhero pose and upload it with facebook')) {
                      window.location.href = 'https://www.facebook.com/sharer.php?u=' + String(dataUri);
                    };
                  }}
                    className='activity_instaicon4' src={facebook} />
                </div>
                <div className='activity__fb2main4'>
                  <img onClick={() => {
                    if (window.confirm('Strike a superhero pose and upload it with twitter')) {
                      window.location.href = 'http://www.twitter.com/share?text=thesearch.bengalurusustainabilityforum.org&url=' + String(dataUri);
                    };
                  }}
                    className='activity_instaicon4' src={twitter} />
                </div>
              </div>
            }

            <div className="button_container">
              <button className="continue_button_Green" style={{ backgroundColor: colourObj.color_green }}
                onClick={() => {
                  sendGamedata(props.id, "SUBLEVEL", inputvalue, "", "DONE", auth)
                  setTimeout(() => {
                    loadActiveSubleveldetails(props.id)
                    loadActiveleveldetails(props.level_id)
                  }, 500);
                  setTimeout(() => {
                    ((Number(((ActiveLevelDetails) && ActiveLevelDetails.progress))) === 100) ?
                      setActivePopup("reward")
                      :
                      setActivePopup("submit")
                  }, 1000);
                }}>
                <div className="button_text" style={{ color: colourObj.color_m1 }}>{(Language?.value === "KANNADA") ? "ಸಲ್ಲಿಸು" : "SUBMIT"}</div>
              </button>
            </div>
          </div>
        }

        {(props.sl_misc === "TEXTBOX") &&
          <div className="activity_actioncontainer">
            <div className="activity_inputcontainer8">
              <textarea id="activity_input_value" placeholder={props?.GameDataLog[0]?.data} className='activity_input8' >
              </textarea>
            </div>
            <div className="button_container">
              <button className="continue_button_Green" style={{ backgroundColor: colourObj.color_green }} onClick={() => {
                console.log(">>", String(document.getElementById("activity_input_value").value));
                sendGamedata(props.id, "SUBLEVEL", String(document.getElementById("activity_input_value").value), "", "DONE", auth)
                setTimeout(() => {
                  loadActiveSubleveldetails(props.id)
                  loadActiveleveldetails(props.level_id)
                }, 500);
                setTimeout(() => {
                  ((Number(((ActiveLevelDetails) && ActiveLevelDetails.progress))) === 100) ?
                    setActivePopup("reward")
                    :
                    setActivePopup("submit")
                }, 1000);
              }}>
                <div className="button_text" style={{ color: colourObj.color_m1 }}>{(Language?.value === "KANNADA") ? "ಸಲ್ಲಿಸು" : "SUBMIT"}</div>
              </button>
            </div>
          </div>
        }

        {(props.sl_misc === "VOICE RECORDER") &&
          <div className="activity_actioncontainer">
            <div className="activity_inputcontainer6">

              {/* <AudioRecord /> */}
              <div className="upload-wrap">
                <button type="button" className="nice-button">{(Language?.value === "KANNADA") ? "ಫೈಲ್ ಅಪ್ಲೋಡ್ ಮಾಡಿ" : "upload file"}</button>
                <input type="file" name="file" className="upload-btn" id="activity_input_value"
                  onChange={_onChangeHandler}
                />
              </div>

            </div>
            <div className="button_container">
              <button className="continue_button_Green" style={{ backgroundColor: colourObj.color_green }}>
                <div onClick={() => {
                  console.log(">>>>>setinputvalue>>>>setinputvalue :", inputvalue);
                  sendGamedata(props.id, "SUBLEVEL", inputvalue, "", "DONE", auth)
                  setTimeout(() => {
                    loadActiveSubleveldetails(props.id)
                    loadActiveleveldetails(props.level_id)
                  }, 500);
                  setTimeout(() => {
                    ((Number(((ActiveLevelDetails) && ActiveLevelDetails.progress))) === 100) ?
                      setActivePopup("reward")
                      :
                      setActivePopup("submit")
                  }, 1000);
                }}
                  className="button_text" style={{ color: colourObj.color_m1 }}>{(Language?.value === "KANNADA") ? "ಸಲ್ಲಿಸು" : "SUBMIT"}</div>
              </button>
            </div>
          </div>
        }

        {(props.sl_misc === "SLIDER") &&
          <div className="activity_actioncontainer">
            <div className="activity_slider">
              <Slider
                defaultValue={0}
                step={null}
                marks={marks}
                value={inputvalue}
                onChange={(data, value) => {
                  console.log(value, data)
                  setinputvalue(value)
                }}
              />
            </div>
            <div className="button_container">
              <button className="continue_button_Green" style={{ backgroundColor: colourObj.color_green }} onClick={() => {
                console.log("inputvalue", inputvalue);
                sendGamedata(props.id, "SUBLEVEL", inputvalue, "", "DONE", auth)
                setTimeout(() => {
                  loadActiveSubleveldetails(props.id)
                  loadActiveleveldetails(props.level_id)
                }, 500);
                setTimeout(() => {
                  ((Number(((ActiveLevelDetails) && ActiveLevelDetails.progress))) === 100) ?
                    setActivePopup("reward")
                    :
                    setActivePopup("submit")
                }, 1000);
              }}>
                <div className="button_text" style={{ color: colourObj.color_m1 }}>{(Language?.value === "KANNADA") ? "ಸಲ್ಲಿಸು" : "SUBMIT"}</div>
              </button>
            </div>
          </div>
        }

        {camera &&
          <>
            <div className='activity_scannerpopup_bg'>
              <div className='activity_scannerpopup_container'>
                <div className='activity_scannerpopup'>
                  <div className='activity_Camersize' onClick={() => {
                    setshare(true)
                  }}>
                    <div className='activity_Camera_div'>
                      <Camera
                        onTakePhotoAnimationDone={_handleTakePhotoAnimationDone}
                        // idealResolution={{ width: 500, height: 990 }}
                        idealFacingMode={idealFacingMode}
                        isMaxResolution={isMaxResolution}
                        isImageMirror={true}
                        isFullscreen={true}
                      />

                    </div>
                    <div className='activitycontainer'>
                      <div onClick={() => { setcamera(false) }}
                        className='activity_close_container'>
                        <img className='activity_close' src={close} />
                      </div>
                      <div className='activity_switchcamera-container'>
                        {idealFacingMode ?
                          <img onClick={(e) => {
                            setIdealFacingMode(!idealFacingMode);
                            // setIsMaxResolution(false);
                            console.log("front")
                          }} className='activity_switchcamera' style={{ backgroundColor: colourObj.color_m2 }} src={switchcamera} />
                          :
                          <img onClick={(e) => {
                            setIdealFacingMode(FACING_MODES.ENVIRONMENT);
                            // setIsMaxResolution(false);
                            console.log("back")
                          }} className='activity_switchcamera' style={{ backgroundColor: colourObj.color_m2 }} src={switchcamera} />
                        }
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </>
        }

        {popup &&
          <>
            <div className="quiz_popup_back">
              <div className="popup_container">
                <div className="popup_closecontainer" style={{ backgroundColor: colourObj.color_m2 }}>
                  <img onClick={() => { setpopup(false) }} className='quiz_close' src={close} />
                </div>
                <div className="popup" style={{ backgroundColor: colourObj.color_t1, borderColor: colourObj.color_m2 }}>

                  <div className='Title_OrangeU' style={{ color: colourObj.color_m1, textDecorationColor: colourObj.color_m2 }}>{`Thanks for the reply.`}
                  </div>
                  <div className='popup_Text' style={{ color: colourObj.color_m1 }}>
                    {` Please find below our offer for your community. OFFER
                                    Enjoy FREE Yulu Rides For A Month!
                                    Use code ‘NEW1500’ & get FREE rides worth Rs.1,500.
                                    Enjoy 30% off each day for the next 30 days `}
                  </div>
                  <div className='popup_Text' style={{ color: colourObj.color_s1 }}>{`Offer T&C 
                      Use Code NEW1500 and get Rs. 1500 upfront in your Yulu Money Account 
                      Coupon Validity - 30 Days 
                      Enjoy 30% discount on trips up to a max of Rs. 50/ day
                      Coupon valid for new users onlyg`}
                  </div>


                </div>
                <div className='popup_continue_buttonContainer'>
                  <button onClick={() => {
                  }} className="continue_button_Orange" style={{ backgroundColor: colourObj.color_m2 }}>
                    <div className="button_text" style={{ color: colourObj.color_m1 }}>Continue</div>
                  </button>
                </div>
              </div>
            </div>
          </>
        }
      </div >

    </div>

  )
}

export default ActivityComponent
