import React, { useEffect, useState } from 'react'
import './style.css'
import * as logout_img from '../../../../assets/logout.svg'
import * as Audio_icon from '../../../../assets/audio.svg'
import * as Audio_icon_ON from '../../../../assets/audio_on.svg'
import * as Vibration_Icon from '../../../../assets/vibration.svg'
import * as Vibration_Icon_ON from '../../../../assets/vibration_on.svg'
import * as kannada from '../../../../assets/kannada.svg'
import * as gameicon from '../../../../assets/mobile-game.png'
import McInput from '../../../Common/McInput'
import { useLanguageStore } from '../../../../store/LanguageStore'
import { userstore } from '../../../../store/userStore'
import { instancestore } from '../../../../store/InstanceStore'
import { useHistory } from 'react-router-dom'
import { useActivePopup } from '../../../../store/ActivePopup';
import { useColourObjectStore } from '../../../../store/ColourStore'
import Popup from 'components/Common/Popup'
import { useAuth } from '../../../../store/authStore'

const useAudio = url => {

  const [audio] = useState(new Audio(url));
  const [playing, setPlaying] = useState(false);
  const toggle = () => setPlaying(!playing);

  useEffect(() => {
    playing ? audio.play() : audio.pause();
  },
    [playing]
  );

  useEffect(() => {
    audio.addEventListener('ended', () => setPlaying(false));
    return () => {
      audio.removeEventListener('ended', () => setPlaying(false));
    };
  }, []);
  return [playing, toggle];
};


const Settings = ({ }) => {

  const { colourObj } = useColourObjectStore();
  const { setAuth, logout } = useAuth()

  const { setActivePopup } = useActivePopup();

  const { selfuser, loadcurrentuser, editprofileuser } = userstore();

  const [playing, toggle] = useAudio("");
  const [vib, setvib] = useState(false)

  const { Language, setLanguage } = useLanguageStore();

  const [instanceid, setinstanceid] = useState('');
  const { instance } = instancestore();
  const history = useHistory()
  const [edit_popup, setedit_popup] = useState(false)


  const [firstname, setfirstname] = useState((selfuser) && selfuser.firstname);
  const [lastname, setlastname] = useState((selfuser) && selfuser.lastname);
  const [email, setemail] = useState((selfuser) && selfuser.email);
  const [image, setimage] = useState((selfuser) && selfuser.image);
  const [photo_url, setphoto_url] = useState((selfuser) && selfuser.photo_url);
  const [dob, setdob] = useState((selfuser) && selfuser.dob);
  const [gender, setgender] = useState({ "value": (selfuser) && selfuser.gender });
  const [address, setaddress] = useState((selfuser) && selfuser.address);
  const [dial_code, setdial_code] = useState((selfuser) && selfuser.dial_code);
  const [phone, setphone] = useState((selfuser) && selfuser.phone);


  useEffect(() => {

    if (!selfuser) {
      loadcurrentuser()
    }

  }, [])


  return (
    <>
      <div className="settings_listitem_container">

        <div className="settings_listitem">
          <div className="card1">
            <img className="profilelogo" style={{ borderColor: colourObj.color_m2 }} src={(selfuser) && selfuser.photo_url} alt="John" />
            <div className="profilename">{(selfuser) && selfuser.firstname + " " + (selfuser) && selfuser.lastname}</div>
            <div className="profileemail">{(selfuser) && selfuser.email}</div>
            <div className="editprofile"><button onClick={() => {
              setedit_popup(true)
            }}>{(Language?.value === "KANNADA") ? "ನಿಮ್ಮ ಪ್ರೊಫೈಲ್ ಸಂಪಾದಿಸಿ" : "EDIT YOUR PROFILE"}</button></div>
          </div>
        </div>

        <div onClick={() => {
          setvib(!vib)
          window.navigator.vibrate([100, 30, 100, 30, 100, 30, 200,
            30, 200, 30, 200, 30, 100, 30, 100, 30, 100])
        }}
          className="settings_listitem">
          <div className="insettings_icon_div" style={{ backgroundColor: colourObj.color_m1 }}>
            {vib ? <img className='insettings_icon' src={Vibration_Icon_ON} /> : <img className='insettings_icon' src={Vibration_Icon} />}
          </div>
          <div className="settings_listtext shadow" style={{ backgroundColor: colourObj.color_s2 }}>
            {(Language?.value === "KANNADA") ? "ಫೋನ್ ವೈಬ್ರೇಶನ್" : "VIBRATION"}</div>

        </div>

        <div onClick={() => { toggle }} className="settings_listitem">
          <div className="insettings_icon_div" style={{ backgroundColor: colourObj.color_m1 }}>
            {playing ? <img className='insettings_icon' src={Audio_icon_ON} /> : <img className='insettings_icon' src={Audio_icon} />}
          </div>
          <div className="settings_listtext shadow" style={{ backgroundColor: colourObj.color_s2 }}>
            {(Language?.value === "KANNADA") ? "ಆಡಿಯೋ ಬಟನ್" : "AUDIO"}</div>
        </div>

        <div onClick={() => {
        }} className="settings_listitem">
          <div className="insettings_icon_div" style={{ backgroundColor: colourObj.color_m1 }}>
            <img className='insettings_icon' src={kannada} />
          </div>
          <div className="settings_listtext shadow" style={{ backgroundColor: colourObj.color_s2 }}>
            <div className="inputfield_sub_container">
              <McInput
                type="picker"
                id="input_i_bodytypetextbox"
                name={`LANGUAGE`}
                required={true} ENGLISH
                value={Language?.value}
                onchange={(data: any) => {
                  setLanguage(data)
                }}
                options={[
                  { "key": "ENGLISH", "value": "ENGLISH" },
                  { "key": "KANNADA", "value": "KANNADA" },
                ]}
                description={"Select your LANGUAGE"}
              />
            </div></div>
        </div>

        <div onClick={() => {
        }} className="settings_listitem">
          <div className="insettings_icon_div" style={{ backgroundColor: colourObj.color_m1 }}>
            <img className='insettings_icon' src={gameicon} />
          </div>
          <div className="settings_listtext shadow" style={{ backgroundColor: colourObj.color_s2 }}>
            <div className="inputfield_sub_container">
              <McInput
                type="picker"
                id="input_i_instance_textbox"
                name={`GAME`}
                required={true}
                value={instanceid?.value}
                onchange={(data: any) => {
                  setinstanceid(data)
                  history.push(`/${data.key}/`)
                  window.location.reload();
                }}
                options={(instance) && instance.map(
                  (obj: any) => {
                    return { "key": obj.id, "value": obj.i_title }
                  }
                )}
                description={"Select your game."}
              />
            </div></div>

        </div>

        <div onClick={() => {
          logout()
          setActivePopup("")
          history.replace('/')
          window.location.reload()
        }} className="settings_listitem">
          <div className="insettings_icon_div" style={{ backgroundColor: colourObj.color_m1 }}>
            <img className='insettings_icon' src={logout_img} />
          </div>
          <div className="settings_listtext shadow" style={{ backgroundColor: colourObj.color_s2 }}>
            {(Language?.value === "KANNADA") ? "ಲಾಗ್ ಔಟ್" : "LOGOUT"}</div>
        </div>

      </div>

      {edit_popup &&

        <Popup
          title={"PROFILE EDIT"}
          popup_body={
            <>
              <div className="main_input_wrap">

                <div className="div_column_wrap">

                  <div className="inputfield_sub_container">
                    <McInput
                      label={"FIRST NAME"}
                      id="input_firstnametextbox"
                      name={`data.firstname`}
                      inputtype="Text"
                      type="text"
                      min_length="3"
                      required={true}
                      value={firstname}
                      onchange={setfirstname}
                      description={"Enter user Firstname"}
                    />
                  </div>

                  <div className="inputfield_sub_container">
                    <McInput
                      label={"LAST NAME"}
                      id="input_lastnametextbox"
                      name={`data.lastname`}
                      inputtype="Text"
                      type="text"
                      min_length="3"
                      required={true}
                      value={lastname}
                      onchange={setlastname}
                      description={"Enter user Lastname"}
                    />
                  </div>

                  <div className="inputfield_sub_container">
                    <McInput
                      label={"EMAIL"}
                      id="input_emailtextbox"
                      name={`data.email`}
                      inputtype="Text"
                      type="text"
                      min_length="3"
                      required={true}
                      value={email}
                      onchange={setemail}
                      description={"Enter user Email-ID"}
                    />
                  </div>

                  <div className="inputfield_sub_container">
                    <McInput
                      label={"IMAGE"}
                      id="input_imagetextbox"
                      name={`data.image`}
                      inputtype="Text"
                      type="text"
                      min_length="3"
                      required={true}
                      value={image}
                      onchange={setimage}
                      description={"Enter user Image"}
                    />
                  </div>

                  <div className="inputfield_sub_container">
                    <McInput
                      label={"PHOTO URL"}
                      id="input_photo_urltextbox"
                      name={`data.photo_url`}
                      inputtype="Text"
                      type="text"
                      min_length="3"
                      required={true}
                      value={photo_url}
                      onchange={setphoto_url}
                      description={"Enter user Photo URL"}
                    />
                  </div>

                </div>

                <div className="div_column_wrap">

                  <div className="inputfield_sub_container">
                    <McInput
                      label={"DOB(yyy-mm-dd)"}
                      id="input_dobtextbox"
                      name={`data.dob`}
                      inputtype="Text"
                      type="text"
                      min_length="3"
                      required={true}
                      value={dob}
                      onchange={setdob}
                      description={"Enter user Date of birth"}
                    />
                  </div>

                  <div className="inputfield_sub_container">
                    <McInput
                      type="picker"
                      id="input_gendertextbox"
                      name={`GENDER`}
                      required={true}
                      value={gender?.value}
                      onchange={(data: any) => {
                        setgender(data)
                      }}
                      options={[
                        { "key": "0", "value": "MALE" },
                        { "key": "1", "value": "FEMALE" },
                        { "key": "2", "value": "OTHER" }
                      ]}
                      description={"Enter user Gender"}
                    />
                  </div>

                  <div className="inputfield_sub_container">
                    <McInput
                      label={"ADDRESS"}
                      id="input_addresstextbox"
                      name={`data.address`}
                      inputtype="Text"
                      type="text"
                      min_length="3"
                      required={true}
                      value={address}
                      onchange={setaddress}
                      description={"Enter user Address"}
                    />
                  </div>

                  <div className="inputfield_sub_container">
                    <McInput
                      label={"DIAL CODE"}
                      id="input_dial_codetextbox"
                      name={`data.dial_code`}
                      inputtype="Text"
                      type="text"
                      min_length="3"
                      required={true}
                      value={dial_code}
                      onchange={setdial_code}
                      description={"Enter user Dial Code"}
                    />
                  </div>

                  <div className="inputfield_sub_container">
                    <McInput
                      label={"PHONE NUMBER"}
                      id="input_phonetextbox"
                      name={`data.phone`}
                      inputtype="Text"
                      type="text"
                      min_length="3"
                      required={true}
                      value={phone}
                      onchange={setphone}
                      description={"Enter user Phone Number"}
                    />
                  </div>

                </div>

              </div>
            </>
          }
          confirmClick={() => {
            let data = {
              firstname: String(document.getElementById("input_firstnametextbox").value),
              lastname: String(document.getElementById("input_lastnametextbox").value),
              email: String(document.getElementById("input_emailtextbox").value),
              image: String(document.getElementById("input_imagetextbox").value),
              photo_url: String(document.getElementById("input_photo_urltextbox").value),
              dob: String(document.getElementById("input_dobtextbox").value),
              gender: gender?.value,
              address: String(document.getElementById("input_addresstextbox").value),
              dial_code: String(document.getElementById("input_dial_codetextbox").value),
              phone: String(document.getElementById("input_phonetextbox").value),
              is_deleted: false,
            }

            editprofileuser(data)
            setedit_popup(false)
          }}
          cancelClick={() => {
            setedit_popup(false)
          }}
        />

      }
    </>
  )
}

export default Settings