import React, { useEffect, useState } from 'react'
import './style.css'
import * as instagram from '../../../../assets/Clean Walk/instagram.png'
import { FacebookShareButton, FacebookIcon, WhatsappIcon, WhatsappShareButton, TwitterShareButton, TwitterIcon, } from "react-share";
import { useLanguageStore } from '../../../../store/LanguageStore'
import { useColourObjectStore } from '../../../../store/ColourStore';

const Share = () => {

  const { Language, setLanguage } = useLanguageStore();
  const { colourObj } = useColourObjectStore();


  return (
    <div className="share_wrapper">

      <div className="share_icons_wrapper">

        <div className='sm_btns_wrapper1'>
          <div className='share_smIcon wa'>
            <WhatsappShareButton
              url={"https://thesearch.bengalurusustainabilityforum.org"}>
              <WhatsappIcon size={'100%'} round={true} />
            </WhatsappShareButton>
          </div>
          <div className='share_smIcon in'>
            <img onClick={() => {
              if (window.confirm('The text has been copied to clipboard whould you like to continue with instagram')) {
                window.location.href = 'https://www.instagram.com/?hl=en';
                navigator.clipboard.writeText("https://thesearch.bengalurusustainabilityforum.org")
              };
            }}
              className='insta_icon' src={instagram} />
          </div>
        </div>
        <div className='sm_btns_wrapper2'>
          <div className='share_smIcon fb'>
            <FacebookShareButton
              url={"https://thesearch.bengalurusustainabilityforum.org"}>
              <FacebookIcon size={'100%'} round={true} />
            </FacebookShareButton>
          </div>
          <div className='share_smIcon twtr'>
            <TwitterShareButton
              url={"https://thesearch.bengalurusustainabilityforum.org"}>
              <TwitterIcon size={'100%'} round={true} />
            </TwitterShareButton>
          </div>
        </div>
      </div>

      <div className="share_text_container">
        <div className='share_TextTitle' style={{ color: colourObj.color_m1 }}>{(Language?.value === "KANNADA") ? "ಆಟವನ್ನು ಆನಂದಿಸಿದ್ದೀರಾ?" : "Enjoying the game?"}</div>
        <div className='share_TextTitle' style={{ color: colourObj.color_m1 }}>{(Language?.value === "KANNADA") ? "ಇತರರಿಗೆ ತಿಳಿಸಿ!" : "Let others know!"}</div>
      </div>

    </div>
  )
}

export default Share


